}

  @media (min-width: 768px) {

  code {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}
  
  pre > code {
    counter-reset: line;
  }
  
  code[data-theme*=" "],
  code[data-theme*=" "] span {
    color: var(--shiki-light);
