/* Liquid Glass Animations */
@keyframes liquidRipple {
  0% {
    width: 4px;
    height: 4px;
    opacity: 0.6;
  }
  100% {
    width: 200px;
    height: 200px;
    opacity: 0;
  }
}

@keyframes liquidWobble {
  0% {
    transform: translate(var(--wobble-start-x, 0), var(--wobble-start-y, 0)) scale(1);
  }
  20% {
    transform: translate(calc(var(--wobble-start-x, 0) * -0.5), calc(var(--wobble-start-y, 0) * -0.5)) scale(1.02);
  }
  40% {
    transform: translate(calc(var(--wobble-start-x, 0) * 0.3), calc(var(--wobble-start-y, 0) * 0.3)) scale(0.98);
  }
  60% {
    transform: translate(calc(var(--wobble-start-x, 0) * -0.15), calc(var(--wobble-start-y, 0) * -0.15)) scale(1.01);
  }
  80% {
    transform: translate(calc(var(--wobble-start-x, 0) * 0.05), calc(var(--wobble-start-y, 0) * 0.05)) scale(0.99);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}

@keyframes unlockSlide {
  0% {
    transform: translateY(0) scale(1);
    opacity: 1;
    filter: blur(0);
  }
  100% {
    transform: translateY(-30px) scale(1.02);
    opacity: 0;
    filter: blur(20px);
  }
}

@keyframes desktopReveal {
  0% {
    transform: scale(0.95);
    opacity: 0;
    filter: blur(20px);
  }
  100% {
    transform: scale(1);
    opacity: 1;
    filter: blur(0);
  }
}

@keyframes floatGentle {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}

@keyframes pulseGlow {
  0%, 100% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.8;
  }
}

@keyframes shimmer {
  0% {
    background-position: -200% center;
  }
  100% {
    background-position: 200% center;
  }
}

/* iOS-style background gradient animation */
@keyframes gradientFlow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.animate-gradient-flow {
  background-size: 200% 200%;
  animation: gradientFlow 15s ease infinite;
}

.animate-float {
  animation: floatGentle 4s ease-in-out infinite;
}

.animate-pulse-glow {
  animation: pulseGlow 3s ease-in-out infinite;
}

.animate-shimmer {
  background: linear-gradient(
    90deg,
    transparent 0%,
    hsl(var(--theme-foreground, 0 0% 100%) / 0.1) 50%,
    transparent 100%
  );
  background-size: 200% 100%;
  animation: shimmer 2s infinite;
}

/* iOS Home Screen Edit Mode Animations */
@keyframes iosWiggle {
  0%, 100% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-2deg);
  }
  75% {
    transform: rotate(2deg);
  }
}

@keyframes iosWiggleAlt {
  0%, 100% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(2deg);
  }
  75% {
    transform: rotate(-2deg);
  }
}

.animate-ios-wiggle {
  animation: iosWiggle 0.25s ease-in-out infinite;
  will-change: transform;
  transform: translateZ(0); /* Force GPU acceleration */
}

.animate-ios-wiggle-alt {
  animation: iosWiggleAlt 0.25s ease-in-out infinite;
  will-change: transform;
  transform: translateZ(0); /* Force GPU acceleration */
}

/* iOS-style drag ghost */
.ios-drag-ghost {
  box-shadow: 0 30px 60px -12px rgba(0, 0, 0, 0.5),
              0 18px 36px -18px rgba(0, 0, 0, 0.4);
}

/* iOS-style folder background blur */
.ios-folder-backdrop {
  backdrop-filter: blur(40px) saturate(180%);
  -webkit-backdrop-filter: blur(40px) saturate(180%);
}

/* iOS-style press feedback */
@keyframes iosTapFeedback {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.92);
  }
  100% {
    transform: scale(1);
  }
}

.animate-ios-tap {
  animation: iosTapFeedback 0.15s ease-out;
}

/* iOS spring bounce effect */
@keyframes iosSpringBounce {
  0% {
    transform: scale(0.3);
    opacity: 0;
  }
  50% {
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.animate-ios-spring {
  animation: iosSpringBounce 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

/* ============================================
   Glow Effects for App Icons
   ============================================ */

/* Hover glow effect */
@keyframes iconHoverGlow {
  0%, 100% {
    box-shadow: 0 0 0 0 hsl(var(--theme-foreground, 0 0% 100%) / 0);
  }
  50% {
    box-shadow: 0 0 20px 5px hsl(var(--theme-foreground, 0 0% 100%) / 0.15);
  }
}

.icon-glow-hover {
  position: relative;
}

.icon-glow-hover::before {
  content: '';
  position: absolute;
  inset: -10px;
  border-radius: inherit;
  background: radial-gradient(circle at center, hsl(var(--theme-foreground, 0 0% 100%) / 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.15s ease-in;
  pointer-events: none;
}

.icon-glow-hover:hover::before {
  opacity: 1;
}

/* Active app glow */
.icon-glow-active::before {
  opacity: 1;
  background: radial-gradient(circle at center, hsl(var(--theme-foreground, 0 0% 100%) / 0.2) 0%, transparent 60%);
}

/* Notification pulse */
@keyframes notificationPulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(239, 68, 68, 0.7);
  }
  70% {
    transform: scale(1.1);
    box-shadow: 0 0 0 6px rgba(239, 68, 68, 0);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(239, 68, 68, 0);
  }
}

.animate-notification-pulse {
  animation: notificationPulse 0.4s ease-out;
}

/* Dynamic Island breathing */
@keyframes islandBreathing {
  0%, 100% {
    transform: scale(1);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.02);
    opacity: 0.8;
  }
}

.animate-island-breathing {
  animation: islandBreathing 3s ease-in-out infinite;
}

/* ============================================
   Reduced Motion Support
   ============================================ */

@media (prefers-reduced-motion: reduce) {
  /* Disable all transform-heavy animations */
  .animate-ios-wiggle,
  .animate-ios-wiggle-alt,
  .animate-ios-spring,
  .animate-ios-tap,
  .animate-island-breathing,
  .animate-notification-pulse,
  .animate-float,
  .animate-gradient-flow,
  .animate-shimmer,
  .animate-pulse-glow {
    animation: none !important;
  }

  /* Keep only simple opacity transitions */
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.15s !important;
    transition-property: opacity !important;
  }

  /* Disable glow effects */
  .icon-glow-hover::before,
  .icon-glow-active::before {
    display: none;
  }

  /* Disable spring physics indicators */
  .spring-bounce {
    animation: none !important;
  }
}

/* ============================================
   Focus Indicators for Accessibility
   ============================================ */

.focus-ring {
  outline: none;
}

.focus-ring:focus-visible {
  outline: 2px solid hsl(var(--theme-primary, 24 90% 55%) / 0.8);
  outline-offset: 2px;
}

/* High contrast focus for better visibility */
@media (prefers-contrast: high) {
  .focus-ring:focus-visible {
    outline: 3px solid hsl(var(--theme-foreground, 0 0% 100%));
    outline-offset: 3px;
  }
}

/* ============================================
   Motion Scale Utility
   ============================================ */

:root {
  --motion-scale: 1;
}

@media (prefers-reduced-motion: reduce) {
  :root {
    --motion-scale: 0;
  }
}

/* Use this for custom motion scaling */
.motion-safe {
  transform: scale(calc(1 + (var(--motion-scale) * 0.05)));
}

/* ============================================
   Safe Area Utilities for Mobile
   ============================================ */

.safe-area-top {
  padding-top: env(safe-area-inset-top, 0);
}

.safe-area-bottom {
  padding-bottom: env(safe-area-inset-bottom, 0);
}

.safe-area-left {
  padding-left: env(safe-area-inset-left, 0);
}

.safe-area-right {
  padding-right: env(safe-area-inset-right, 0);
}

/* ============================================
   Claw AI Orb Animations
   ============================================ */

@keyframes orbPulse {
  0%, 100% {
    box-shadow: 0 0 30px rgba(249, 115, 22, 0.4),
                0 0 60px rgba(251, 191, 36, 0.2),
                inset 0 0 15px rgba(255, 255, 255, 0.15);
  }
  50% {
    box-shadow: 0 0 50px rgba(249, 115, 22, 0.6),
                0 0 100px rgba(251, 191, 36, 0.3),
                inset 0 0 25px rgba(255, 255, 255, 0.25);
  }
}

@keyframes orbRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes orbFloat {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-6px);
  }
}

.animate-orb-pulse {
  animation: orbPulse 3s ease-in-out infinite;
}

.animate-orb-rotate {
  animation: orbRotate 8s linear infinite;
}

.animate-orb-float {
  animation: orbFloat 3s ease-in-out infinite;
}

/* Chat overlay slide animation */
@keyframes slideUpSheet {
  from {
    transform: translateY(100%);
    opacity: 0.5;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.animate-slide-up-sheet {
  animation: slideUpSheet 0.3s cubic-bezier(0.32, 0.72, 0, 1);
}

/* Typing dots animation */
@keyframes typingDot {
  0%, 60%, 100% {
    transform: translateY(0);
    opacity: 0.5;
  }
  30% {
    transform: translateY(-4px);
    opacity: 1;
  }
}

.animate-typing-dot {
  animation: typingDot 1s ease-in-out infinite;
}

/* Disable orb animations in reduced motion */
@media (prefers-reduced-motion: reduce) {
  .animate-orb-pulse,
  .animate-orb-rotate,
  .animate-orb-float,
  .animate-slide-up-sheet,
  .animate-typing-dot {
    animation: none !important;
  }
}

/* ============================================
   Theme-Aware Utility Classes
   Uses CSS variables from design theme system
   ============================================ */

/* Background colors */
.bg-theme-primary {
  background-color: hsl(var(--theme-primary));
}

.bg-theme-primary-hover:hover {
  background-color: hsl(var(--theme-primary) / 0.9);
}

.bg-theme-accent {
  background-color: hsl(var(--theme-accent));
}

.bg-theme-secondary {
  background-color: hsl(var(--theme-secondary));
}

.bg-theme-card {
  background-color: hsl(var(--theme-card));
}

.bg-theme-muted {
  background-color: hsl(var(--theme-muted));
}

/* Translucent backgrounds */
.bg-theme-primary\/10 {
  background-color: hsl(var(--theme-primary) / 0.1);
}

.bg-theme-primary\/20 {
  background-color: hsl(var(--theme-primary) / 0.2);
}

.bg-theme-primary\/30 {
  background-color: hsl(var(--theme-primary) / 0.3);
}

/* Text colors */
.text-theme-primary {
  color: hsl(var(--theme-primary));
}

.text-theme-accent {
  color: hsl(var(--theme-accent));
}

.text-theme-foreground {
  color: hsl(var(--theme-foreground));
}

.text-theme-muted {
  color: hsl(var(--theme-muted-foreground));
}

/* Border colors */
.border-theme-primary {
  border-color: hsl(var(--theme-primary));
}

.border-theme-primary\/50 {
  border-color: hsl(var(--theme-primary) / 0.5);
}

.border-theme-border {
  border-color: hsl(var(--theme-border));
}

/* Focus states */
.focus\:border-theme-primary:focus {
  border-color: hsl(var(--theme-primary));
}

.focus\:ring-theme-primary:focus {
  --tw-ring-color: hsl(var(--theme-primary) / 0.5);
}

/* Gradients */
.bg-gradient-theme {
  background: linear-gradient(135deg, hsl(var(--theme-primary)) 0%, hsl(var(--theme-accent)) 100%);
}

.bg-gradient-theme-horizontal {
  background: linear-gradient(90deg, hsl(var(--theme-primary)) 0%, hsl(var(--theme-accent)) 100%);
}

.bg-gradient-theme-vertical {
  background: linear-gradient(180deg, hsl(var(--theme-primary)) 0%, hsl(var(--theme-accent)) 100%);
}

.text-gradient-theme {
  background: linear-gradient(135deg, hsl(var(--theme-primary)) 0%, hsl(var(--theme-accent)) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Shadows */
.shadow-theme-primary {
  box-shadow: 0 10px 25px -5px hsl(var(--theme-primary) / 0.3);
}

.shadow-theme-primary-lg {
  box-shadow: 0 20px 40px -10px hsl(var(--theme-primary) / 0.4);
}

/* Hover variants */
.hover\:bg-theme-primary:hover {
  background-color: hsl(var(--theme-primary));
}

.hover\:text-theme-primary:hover {
  color: hsl(var(--theme-primary));
}

.hover\:border-theme-primary:hover {
  border-color: hsl(var(--theme-primary));
}

.hover\:shadow-theme-primary:hover {
  box-shadow: 0 15px 35px -5px hsl(var(--theme-primary) / 0.4);
}

/* Hide scrollbar utility */
.scrollbar-hide {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.scrollbar-hide::-webkit-scrollbar {
  display: none;  /* Chrome, Safari and Opera */
}

/* Touch Target Utilities - Ensure minimum 44x44px touch targets for accessibility */

/* Touch equivalents for hover states - enable on touch devices */
@media (hover: none) and (pointer: coarse) {
  /* Make hover-dependent elements visible on touch */
  .group:active .group-hover\:opacity-100,
  .group:focus-within .group-hover\:opacity-100 {
    opacity: 1;
  }

  .group:active .group-hover\:translate-x-1,
  .group:focus-within .group-hover\:translate-x-1 {
    transform: translateX(0.25rem);
  }

  /* Active state as hover equivalent */
  [class*="hover\:"]:active {
    /* Applies hover styles on active/touch */
  }
}

/* Terminal Block Styling for Blog Posts */
.terminal-block {
  background: #0a0a0a;
  border: 1px solid #22c55e33;
  border-radius: 12px;
  overflow: hidden;
  margin: 2rem 0;
  box-shadow: 0 0 40px rgba(34, 197, 94, 0.1), inset 0 0 60px rgba(0, 0, 0, 0.5);
}

.terminal-block::before {
  content: "●  ●  ●";
  display: block;
  padding: 8px 12px;
  background: #111;
  color: #22c55e44;
  font-size: 10px;
  letter-spacing: 4px;
  border-bottom: 1px solid #22c55e22;
}

.terminal-block pre {
  background: transparent !important;
  margin: 0 !important;
  padding: 1.5rem !important;
  color: #22c55e !important;
  text-shadow: 0 0 10px rgba(34, 197, 94, 0.5);
  font-family: 'SF Mono', 'Fira Code', 'Consolas', monospace !important;
}

.terminal-block code {
  color: #22c55e !important;
  background: transparent !important;
}

/* Flame wobble animation */
@keyframes flameWobble {
  0%, 100% {
    transform: translateX(0) rotate(0deg) scale(1);
  }
  25% {
    transform: translateX(-2px) rotate(-5deg) scale(1.05);
  }
  50% {
    transform: translateX(2px) rotate(3deg) scale(0.95);
  }
  75% {
    transform: translateX(-1px) rotate(-2deg) scale(1.02);
  }
}

.flame-wobble {
  display: inline-block;
  animation: flameWobble 0.5s ease-in-out infinite;
}

.flame-wobble:nth-child(2n) {
  animation-delay: 0.1s;
}

.flame-wobble:nth-child(3n) {
  animation-delay: 0.2s;
}

/* ASCII diagram styling */
.ascii-diagram {
  font-family: 'SF Mono', 'Fira Code', 'Consolas', monospace;
  line-height: 1.4;
  overflow-x: auto;
  white-space: pre;
}

/* Plato's cave fire glow */
.cave-fire {
  text-shadow: 0 0 10px #ff6b00, 0 0 20px #ff4500, 0 0 30px #ff0000;
  animation: fireGlow 0.3s ease-in-out infinite alternate;
}

@keyframes fireGlow {
  from {
    text-shadow: 0 0 10px #ff6b00, 0 0 20px #ff4500, 0 0 30px #ff0000;
    filter: brightness(1);
  }
  to {
    text-shadow: 0 0 15px #ffaa00, 0 0 25px #ff6b00, 0 0 35px #ff4500;
    filter: brightness(1.2);
  }
}

/* Terminal green glow text */
.terminal-glow {
  color: #22c55e;
  text-shadow: 0 0 5px #22c55e, 0 0 10px rgba(34, 197, 94, 0.5);
}

/* Dimension label styling */
.dimension-label {
  font-family: 'SF Mono', 'Fira Code', 'Consolas', monospace;
  letter-spacing: 0.2em;
  text-transform: uppercase;
}
